@import url(https://fonts.googleapis.com/css2?family=Diplomata+SC&family=Josefin+Sans:wght@100&family=Mulish:ital,wght@1,300&family=Piazzolla:opsz,wght@8..30,200;8..30,300;8..30,400&display=swap);
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "muli";
}
.navbar{
  background-color: #fff !important;
}
.navbar-brand{
  font-size: 1.9rem !important;
  color: #565387;
}
.navbar-brand img{
  width: 70px;
  height: 55px;
}
.navbar a{
  font-size: 1.3rem;
  text-transform: capitalize;
}
.menu-active{
  font-weight: bold;
  border-bottom: 1px solid #565387;
}
.navbar a:hover{
  color: #3498db !important;
}
@media (max-width:991px) {
  .navbar-brand{
    font-size: 1.5rem !important;
  }
  .menu-active{
    font-weight: bold;
    border-bottom: none;
  }
  
}
/*########### HOME CSS ###############*/
#header{
  width: 100%;
  height: 80vh;
  margin-top: 70px;
}
#header h1{
  margin: 0 010px 0;
  font-size: 48px;
  font-weight: 700;
  color: black;
}
#header .brand-name{
  color: #3498db;
}
#header h2{
  color: #484848;
  font-size: 24px;
}
#header .get-btn{
  font-weight: 500;
  font-size: 16px;
  letter-spacing: 1px;
  display: inline-block;
  padding: 10px 30px;
  border: 2px solid #3489db;
  border-radius: 50px;
  transition: 0.5s;
  color: #3498db;
  text-decoration: none;
}
#header .get-btn:hover{
  color: wheat;
  background-color: #3498db;

}
.header-img{
  text-align: right;
}
#header .animated{
  animation: up-down 2s ease-in-out infinite alternate-reverse both;
}
@-webkit-keyframes up-down{
  0%{
    -webkit-transform: translateY(10px);
            transform: translateY(10px);
  }
  100%{
    -webkit-transform: translateY(-10px);
            transform: translateY(-10px);
  }
}
@keyframes up-down {
  0%{
    -webkit-transform: translateY(10px);
            transform: translateY(10px);
  }
  100%{
    -webkit-transform: translateY(-10px);
            transform: translateY(-10px);
  }
  
}
.card img{
  height: 200px;
}

